.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center; 
    position: fixed;
    top: 0;
    left: -100%;
    transition: 2850ms;

    /* z-index: 1; */
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-submenu-spacer {
    height: 140px;
}

.nav-submenu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center; 
    position: fixed;
    top: 0;
    left: -100%;
    /* transition: 850ms; */
}

.nav-submenu.active {
    left: 250px;
    /* transition: 350ms; */
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    white-space: nowrap;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
    z-index: 2;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

span {
    margin-left: 16px;
}